body {
  font-family: "Open Sans", sans-serif;
}
h1, h2, h3, h4, h5, h6 {
  font-family: "Raleway", sans-serif;
}
/* start of the style of the navbar in that app */
.navbar .navbar-brand
{
  position: relative;
  font-size: 33px;
  cursor: pointer;
  color: #fff;
  letter-spacing: 2px;
}
.navbar .navbar-list-container ul li
{
  padding: 20px;
  position: relative;
  margin: 4px;
}
.navbar .navbar-list-container ul li a:hover::before
{
  opacity: 1;
  width: 70%;
  visibility:visible;
} 
.navbar .navbar-list-container ul li a:hover 
{
  color: rgb(250, 158, 72);
}
.navbar .navbar-list-container ul li a::before
{
  position: absolute;
  content: " ";
  bottom: -20%;
  width: 0px;
  height: 2px;
  background-color: rgb(235, 141, 54);
  transition: .5s ease-in-out;
  opacity: 0;
  visibility: hidden;
}
.navbar .navbar-list-container ul li a.active::before
{
  width: 70%;
  visibility: visible;
  opacity: 1;
} 
.navbar .navbar-list-container ul li a.active 
{
  color: rgb(235, 141, 54);
}
.navbar .navbar-list-container ul li a
{
  font-size: 16px;
  font-weight: 500;
  text-decoration:none;
  color: rgb(209, 209, 209);
  transition:all .3s ease-in-out;
} 
.navbar .navbar-list-container ul 
{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction:row;
}
.navbar .started-div .fa 
{
  display: none;
}
.navbar .started-div .get-started-btn:hover
{
  background-color: rgb(250, 158, 72);
} 
.navbar .started-div .get-started-btn 
{
  position: relative;
  color: #fff;
  background-color:  rgb(235, 141, 54);
  text-decoration: none;
  padding: 8px 20px;
  border-radius: 50px;
  transition: .3s;
}
.navbar .started-div 
{
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
}
#hero h4
{
  font-size: 17px;
}
#hero h3
{
  color: rgb(250, 158, 72);
}
#hero h4,
#hero h2
{
  color: #fff;
  text-align: center;
}
#hero a 
{
  position: relative;
  color: #fff;
  background-color:  rgb(235, 141, 54);
  text-decoration: none;
  padding: 8px 20px;
  font-size: 20px;
  border-radius: 50px;
  transition: .3s;
}
#hero .col-12 
{
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  row-gap: 10px;
  flex-direction: column;
  margin-top: 10%;
}
#hero 
{
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  height: 100%;
}
@media screen and (max-width:764px){
  #hero .col-12
  {
    transform: translateX(-60px);
  }
}
@media screen and (max-width:663px){
  #hero .col-12
  {
    transform: translateX(-20px);
  }
}
@media screen and (max-width:581px){
  #hero .col-12
  {
    transform: translateX(0px);
  }
}
@media screen and (max-width:462px){
  #hero .col-12 h4
  {
    font-size: 16px;
  }
  #hero .col-12 h3
  {
    font-size: 20px;
  }
  #hero .col-12 h2 
  {
    font-size: 22px;
  }
}
@media screen and (max-width:873px){
  #hero .col-12
  {
    margin-top: -100px;
  }
  #hero 
  {
    justify-content: center;
  }
}
.navbar.bg-navbar
{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  background-color: rgb(0, 0, 0);
  z-index: 4;
}
.navbar 
{ 
  padding-left: 40px;
  padding-right: 40px;
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  border-bottom: 1px solid rgba(237, 237, 237, 0.07);
  transition: .4s;
}
.header-wrapper 
{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 670px;
  background:linear-gradient(rgba(0,0,0,0.4), rgba(0,0,0,0.4)), url('assets_folder/imgs/hero1.jpg');
  background-position: center right;
  background-size: cover;
  background-repeat: no-repeat;
  margin-bottom: 100px;
}
@media screen and (max-width:470px){
  .header-wrapper
  {
    background-position: center right;
  }
}
@media screen and (max-width:870px){
  .navbar .started-div .fa-bars
  {
    display: block;
    color: #fff;
    cursor: pointer;
    font-size: 20px;
    margin-left: 10px;
  }
  .navbar .navbar-list-container ul
  {
    display: none;
  }
}

.navbar.navbar-mobile .navbar-list-container ul li a:hover:before
{
  opacity: 0;
}
.navbar.navbar-mobile .navbar-list-container ul li a.active::before
{
  opacity: 0;
}
.navbar.navbar-mobile .navbar-list-container ul
{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.navbar.navbar-mobile .navbar-brand
{
  display: none;
}
.navbar.navbar-mobile .started-div .fa-bars 
{
  display: none;
} 
.navbar.navbar-mobile .started-div .fa-times 
{
  display: block;
  cursor: pointer;
  font-size: 20px;
  color: #000;
  position: absolute;
  top: 2%;
  right: 8%;
}
.navbar.navbar-mobile .started-div .get-started-btn
{
  display: none;
}
.navbar.navbar-mobile 
{
  position: fixed;
  top: 15%;
  left: 4%;
  width: 360px;
  height: 450px;
  margin: 0 auto;
  right: 4%;
  bottom: 4%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  background-color: #fff;
  z-index: 66;
  border-radius: 10px;
  box-shadow: 20px 30px 95px 4px rgba(0, 0, 0, 0.131);
} 
/* end of that style */

/* start of the style of about section */
.section-tittle h1
{
  position: relative;
  margin-bottom: 40px;
}
.section-tittle h1::before
{
  content: "";
  position: absolute;
  bottom: -20px;
  width: 40px;
  height: 4px;
  background-color: rgb(235, 141, 54);
}
.about ul li i 
{
  transform: translateY(-10px);
  font-size: 20px;
  color: rgb(235, 141, 54);
  margin-right: 10px;
}
.about ul li 
{
  margin-top: 5px;
}
.about ul 
{
  list-style: none;
  transform: translateX(-35px);
}
.about .container a:hover
{
  color: #fff;
  background-color: rgb(250, 158, 72);
  border-color: rgb(250, 158, 72);
} 
.about .container a 
{
  position: absolute;
  color: rgb(235, 141, 54);
  padding: 12px 32px;
  border: 2px solid rgb(235, 141, 54);
  text-decoration: none;
  border-radius: 50px;
  transition: .4s;
  /* margin-top: 20px; */
  transform: translateY(-40px);
}
.about 
{
  margin-top: 670px;
  padding-top: 100px;
  color: #fff;
  margin-bottom: 200px;
}
/* end of that style */

/* start of the style of the skills */
.skills svg:first-child
{
  margin: 0px;
} 
.skills svg 
{
  font-size: 40px;
  margin: 10px;
  /* width: 100px; */
}
@media screen and (max-width:770px){
  .skills svg 
  {
    font-size: 40px;
    margin: 2px;
    /* width: 100px; */
  }
}
.skills 
{
  margin-bottom: 100px;
  position: relative;
  width: 100%;
  color: #fff;
}
/* end of that part */

/* start of the style of the services */
.services .services-box div p 
{
  font-size: 14px;
  margin-top: 10px;
} 
.services .services-box div h2 
{
  font-size: 24px;
  margin-top: 20px;
  transition: all .4s ease-in-out;
} 
.services .services-box div .fa 
{
  font-size: 35px;
  border-radius: 50%;
  color: rgb(235, 141, 54);
  position: absolute;
  left: 50px;
  top: 70px;
  transition: .3s ease-in-out;
}
.services .services-box div path
{
  transition: .3s ease-in-out;
}
.services .services-box div:hover path
{
  fill: rgb(235, 141, 54);
}
.services .services-box div:hover .fa 
{
  color: #fff;
}
.services .services-box div:hover h2 
{
  color: rgb(235, 141, 54);
}
.services .services-box div::before
{
  content: '';
  position: absolute;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0;
  border-bottom: 4px solid rgb(235, 141, 54);
  border-radius: 20px;
  transition: .4s ease-in;
  opacity: 0;
  visibility: hidden;
}
.services .services-box div:hover
{
  transform: translateY(-20px);
}
.services .services-box div:hover::before
{
  visibility: visible;
  opacity: 1;
}
.services .services-box div
{
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  padding-left: 20px;
  padding-top: 40px;
  padding-bottom: 40px;
  border-radius: 20px;
  margin-top: 30px;
  cursor: pointer;
  transition: .4s ease-in-out;
  box-shadow: 1px 1px 95px 0px rgba(61, 61, 61, 0.46);
  border: 1px solid rgba(255, 255, 255, 0.206);
}
.services 
{
  margin-bottom: 100px;
  color: #fff;
}
/* end of the style of the services */


/* this the startof the style of the portfolio */
.portfolio ul li.filter-active 
{
  color: rgb(235, 141, 54);
} 
.portfolio ul li:hover
{
  color: rgb(250, 158, 72);
} 
.portfolio ul li 
{
  margin: 20px;
  cursor: pointer;
  font-weight: 500;
  transition: all .4s;
  color: #fff;
}
.portfolio ul
{
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  list-style: none;
}
.portfolio .portfolio-container .portfolio-item .data-content .fa
{
  color:#f3f9fd;
  cursor: pointer;
  font-size: 20px;
}
.portfolio .portfolio-container .portfolio-item .data-content p
{
  color:#f3f9fd;
}
.portfolio .portfolio-container .portfolio-item .data-content h3
{
  color: #fff;
}
.portfolio .portfolio-container .portfolio-item .data-content::before
{
  content: "";
  width: 40px;
  height: 40px;
  border-left: 2px solid #fff;
  border-top: 2px solid #fff;
  position: absolute;
  left: 4%;
  top: 2%;
}
.portfolio .portfolio-container .portfolio-item .data-content::after
{
  content: "";
  width: 40px;
  height: 40px;
  border-right: 2px solid #fff;
  border-bottom: 2px solid #fff;
  position: absolute;
  right: 4%;
  bottom: 2%;
}
.portfolio .portfolio-container .portfolio-item .data-content
{
  position: absolute;
  top: 0;
  left: 3%;
  bottom:-100%;
  right: 3%;
  background-color: rgba(235, 142, 54, 0.535);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transition: .4s ease-out;
  visibility: hidden;
  transform: scale(.4);
  opacity: 0;
}
.portfolio .portfolio-container .portfolio-item:hover .data-content
{
  visibility: visible;
  opacity: 1;
  bottom: 0;
  transform: scale(1);
}
.portfolio .portfolio-container .portfolio-item
{
  position: relative;
}
.portfolio .portfolio-container
{
  position: relative;
  width: 100%;
}
.portfolio 
{
  position: relative;
  width:100%;
  margin-bottom: 100px;
}
/* end of the style of that part */
