}
.contact 
{
  margin-bottom: 100px;
  color: #fff;
}
/* end of that style */

/* start of the style of the  section footer */
.footer .container ul li .fa 
{
  color: #fff;
} 
.footer .container ul li:hover
{
  background-color: rgb(250, 158, 72);
} 
.footer .container ul li 
{
  background-color: rgb(235, 141, 54);
