}
.footer .container
{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.footer
{
  position: relative;
  width: 100%;
  background-color: #000;
}
/* end  */


.whole-app 
{
  background: rgba(55, 54, 54, 0.981);
}
.loading-sec svg
